
.coin-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #191B1F;
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    margin: 1.5rem 1rem;
    padding: 0.5rem 1rem;
    color: white;
  
    
}

.coin-row:hover {
    transform: scale(1.04);
    transition: .3s ease-in-out;
    cursor: pointer;
}

img {
    height: 40px;
    margin-right: 8px;
    width: auto;
}

.img-symbol {
    display: flex;
    align-items: center;
    margin-left: -1rem;
    width: 120px;
}

.coin-price{
    
    width: 70px;
}
.coin-volume{
    width: 100px;
}



.red {
    color: #f00606;
}
  
.green {
    color: #11d811;
}

@media screen and (max-width: 720px) {
    .hide-mobile {
        display: none;
    }

}